
    body {
        margin: 0px;
    }
    nav {
        position:fixed;
        top:0px;
        left: 0px;
        width: 100%;
        height: 40px;
        background: #E0EAE8;
        text-align: right;
        line-height: 40px;
        z-index: 1;
        
    }
    nav > a {
        text-decoration: none;
       
    }
    
    nav >a> span{
        margin-right: 50px;
        vertical-align: middle;
        color: #01807F;
        font-family: sans-serif;
        
    }

   

     section#welcome-section {
        display: flex;
        align-items: center;
        justify-content: center;
        background: #E0EAE8;
        height: 100vh;
        width: 100%;
    }

    section#welcome-section > h1 {
        font-family:sans-serif;
        font-size: 32px;
        text-align: center;
        line-height: 1.3;
    }
    section#welcome-section > h1 > span {
        font-family: 'Courier New', Courier, monospace;
        font-size: 20px;
        color: #008000;
        
    }


   #aboutme {   
       background-color: rgba(109, 178, 103,0.9);
       color: white;
       margin: 30px;
       padding-top: 30px ;
       padding-bottom: 40px;
       box-shadow: 0 4px 8px 0 rgba(0,0,0,0.3);
       
   }

   #header {
       text-align: center;
       font-size: 30px;
       padding-bottom: 30px ;
   }
   #content {
       display: flex;
       justify-content: center;
   }

   #text {
       width: 55%;
       font-size: 1.3em;
       text-align: justify;
       margin-right: 30px;
       
   }
   #image {
       display: flex;
       margin-left: 1%;
       width: 30%;
   }

   .image {
       height: 250px;
       width: 250px;
       border-radius: 50%;
       margin: auto;

   }

   @media (max-width:1000px){
        .image{
            height: 200px;
       width: 200px;
        }
   }


    #project-section {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 40px;
        padding-bottom: 20px;
        margin: 30px;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.3);
        background-color: rgba(204, 247, 203,0.2);
      }
  
      #project-header {
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-size: 25px;
        text-align: center;
        color: #01807F;
      }
   
      #projects {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 20px;
        padding-top: 20px;
        padding-bottom: 20px;
        
      }
      
      @media (max-width:1000px){
        #projects{
            
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 20px;
            padding-top: 20px;
            padding-bottom: 20px;
        }
       }
      
  
      #project-showAll {
        box-shadow: 0 -2px -4px 0 rgba(0,0,0,0.2);
        border-radius: 5px;
        transition: 0.6s;
        text-align: center;
        cursor: pointer;
        background-color: white;
        color: black;
        padding: 10px;
      }
      #project-section-link {
          text-decoration: none;
          
      }
  
  
      #project-showAll:hover {
        background-color: rgba(61, 59, 59, 0.2);
        box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
      }
  
   
    .project-tile{
        border: solid;
        border-color: #E0EAE8;
        width: 270px;
        height: 200px;
        padding: 10px;
        overflow: hidden;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        border-radius: 5px;
        background-color: white;
    }

    .project-tile > a {
        text-decoration: none;
    }
    .project-tile >a> img{
        width: 80%;
        display: block;
        height: 170px;
        margin: auto;
        transition: all 0.6s;
    }

    .project-tile > a > img:hover {
      transform: scale(1.2);
    }
    
    .project-tile > a> h2{
        font-family: sans-serif;
        font-size: 15px;
        text-align: center;
        color: #01807F;
        border-top: solid 1px #E0EAE8;
        padding-top: 5px;
    }



  


   
   





#experience {
    background-color: rgba(226, 212, 212, 0.3);
    
    margin: 30px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.3);
    padding-top: 20px ;
}
    #experience-header {
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      font-size: 25px;
      text-align: center;
